/* MODAL */
.modal-content {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    pointer-events: auto;
    background-color: #ff004f;
    background-clip: padding-box;
    border: 1px solid rgba(0,0,0,.2);
    border-radius: 0rem;
    outline: 0;    
}

.modal-title{
    color: #fff;
}

.modal-header{
    border-bottom: 0px solid #dee2e6;
    text-align: center;
    display: block;
    padding: 1rem 1rem 0rem 1rem;
}

.modal-footer {
    border-top: 0px solid #dee2e6;
}

.modal-body{
    text-align: center;
    padding: 0rem 1rem 1rem 1rem;
    font-weight: 700;
}

.input-modal-fxy{
    padding-top: 15px;
    margin: 0;
    border: 0;
    border-radius: 3px 3px 3px 3px;
    padding: 16px 18px;
    background: #fff;
    width: 80%;
    font: 14px/24px 'Exo',sans-serif;
    display: inline-block;
    vertical-align: top;
    font-family: 'Exo 2', sans-serif;
    
}

.input-modal-fxy:focus{
    outline: none;
}

